
h1 {
  font-size: 3rem;
  font-weight: 500;
}

h2 {
  font-size: 1.5rem;
  font-weight: 400;
  color: #d1d1d1;
  margin-bottom: 5rem;
}

p,ul {
  font-size: 1rem;
  max-width: 40ch;
  line-height: 1.7;
}

ul {
  margin-top: 1rem;
  line-height: 1.4;
}

li {
  margin-bottom: 0.8rem;
  list-style: none;
}

li > *:not(:first-child) {
  font-weight: 300;
}

#description {
  transition: all 0.5s ease;
}

#controls > * {
  display: flex;
  align-items: center;
}

label {
  margin-right: 0.5rem;
}

.dimmed {
  opacity: 0.5;
}